import { LogoGithub, PeopleOutline, MailOutline, LogoWechat, LogoVk } from "@vicons/ionicons5";
export default defineComponent({
  setup() {
    return {
      LogoGithub,
      PeopleOutline,
      MailOutline,
      LogoWechat,
      LogoVk
    };
  },
  data() {
    return {
      following: 0,
      followers: 0,
      public_repos: 0
    };
  },
  mounted() {
    axios.get("https://api.github.com/users/spbgzh").then(response => {
      console.log(response.data);
      this.following = response.data.following;
      this.followers = response.data.followers;
      this.public_repos = response.data.public_repos;
    }).catch(error => {
      console.log(error);
    });
  }
});